	module.exports = {"data":{"talkJson":{"description":"Rob Pike\n\nConcurrency is the key to designing high performance network services. Go's concurrency primitives (goroutines and channels) provide a simple and efficient means of expressing concurrent execution. In this talk we see how tricky concurrency problems can be solved gracefully with simple Go code.\n\nFor all I/O 2012 sessions, go to https://developers.google.com/io/","meta":{"duration":"PT51M27S"},"presenter":{"name":null},"slug":"google-io-2012-go-concurrency-patterns","source":{"id":"f6kdp27TYZs","provider":"youtube"},"tag":["gdl","cloud"],"title":"Google I/O 2012 - Go Concurrency Patterns"},"allTalkJson":{"edges":[{"node":{"description":"Brad Green, Miško Hevery\n\nWe'll cover the design decisions that drove creating AngularJS, the JavaScript framework that combines data binding, reusable components, dependency injection, and a host of other powerful capabilities for building modern web applications. Come join us and learn how to build extremely scalable applications that are fast to build, trivial to change and maintain, easy to test, and that clearly express your intent to all developers on your team.\n\nFor all I/O 2013 sessions, go to https://developers.google.com/live","meta":{"duration":"PT40M"},"presenter":{"name":null},"source":{"id":"HCR7i5F5L8c","provider":"youtube"},"slug":"google-io-2013-design-decisions-in-angularjs","tag":["chrome","gdl","i-o"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/HCR7i5F5L8c/default.jpg","height":90,"width":120}},"title":"Google I/O 2013 - Design Decisions in AngularJS"}},{"node":{"description":"Sameer Ajmani \n\nConcurrency is the key to designing high performance network services. This talk expands on last year's popular Go Concurrency Patterns talk to dive deeper into Go's concurrency primitives, and see how tricky concurrency problems can be solved gracefully with simple Go code.\n\nFor all I/O 2013 sessions, go to https://developers.google.com/live","meta":{"duration":"PT34M11S"},"presenter":{"name":null},"source":{"id":"QDDwwePbDtw","provider":"youtube"},"slug":"google-io-2013-advanced-go-concurrency-patterns","tag":["gdl","i-o","cloud"],"thumbnails":{"default":{"url":"https://i.ytimg.com/vi/QDDwwePbDtw/default.jpg","height":90,"width":120}},"title":"Google I/O 2013 - Advanced Go Concurrency Patterns"}}]}},"pathContext":{"slug":"google-io-2012-go-concurrency-patterns","tags":["gdl","cloud"]}}

